.section-area {
    /* background: url(../../images/3.jpg) no-repeat center center / cover; */
    padding: 18px 0;
    position: relative;
    z-index: 1;
}
.section-area:before {
    position: absolute;
    content: "";
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    background: rgba(94, 154, 140, 1);
    z-index: -1;
}


.section-area h2 {
    font-size: 60px;
    text-align: center;
    /* border: 1px solid rgba(255,255,255, 0.2); */
    padding: 0px 25px;
    display: inline-block;
    color:#fff;
    margin-bottom: 0px;
}

.clock-area {
    background: #5e9a8d;
    padding: 20px 5px 20px 20px;
}
.footer-area {
  background: url("https://media.giphy.com/media/3ohze3kG5qO9DcTUbe/giphy.gif") no-repeat center center / cover;
  padding: 100px 0;
  position: relative;
  z-index: 1;
}

.footer-area:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: .5;
  z-index: -1;
}

.footer-content {
  margin: auto;
  text-align: center;
}

.content-sub {
  /* padding: 50px 80px; */
  margin: 10px;
  padding: 10px;
  /* background: rgba(94, 154, 142, .5); */
  display: inline-block;
  z-index: 99;
  position: relative;
}

.content-sub:before {
  position: absolute;
  content: "";
  left: -8px;
  top: -6px;
  width: 103%;
  height: 105%;
  /* border: 1px solid rgba(255,255,255, 0.4); */
}

.content-sub h2 {
  font-size: 80px;
  font-family: 'Great Vibes', cursive;
  color: #fff;
}

.content-sub span {
  font-size: 30px;
  color: #dffff9;
}

ul.smothscroll {
    position: fixed;
    bottom: 15px;
    right:30px;
    list-style: none;
    z-index: 999;
}
ul.smothscroll a {
    width: 50px;
    height: 50px;
    background:rgba(30,130,103,.6);
    color: #fff;
    display: block;
    border-radius: 50%;
    text-align: center;
    line-height: 47px;
    border:1px solid rgba(30,130,103);
    transition: all .3s;
}
ul.smothscroll a:hover{
    background:rgba(30,130,103);
}
h1,h2,h3{
  font-family: 'Great Vibes', cursive;
}
body{
  font-family: 'Muli', sans-serif;
}
ul{
  list-style: none;
  margin-bottom: 0!important;
}
a{
  text-decoration: none;
  transition: all .3s;
}
a:hover{
  text-decoration: none!important;
}
img{
  width: 100%;
}
.section-padding{
  padding: 120px 0;
}

.App {
  text-align: left;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media(max-width:767px){
  .section-padding {
    padding: 80px 0;
}
}




